.App {
  text-align: center;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,ol,li{
  list-style: none;
}
a{
  text-decoration: none;
  color: inherit;
}
img{
  vertical-align: top;
  max-width: 100%;
}
#header{
  width: 100%;
  height: 122px;
  position: relative;
  z-index: 10;
  background: transparent;
}
#header .up{
  width: 1100px;
  height: 78px;
  margin: 0 auto;
  position: relative;
}
#logo{
  width: 260px;
  height: 60px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#logo a{
  display: block;
  height: 60px;
  background-image: var(--logo-img);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.util{
  float: right;
}
.util ul{
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 24px;
  font-size: 14px;
  color: #333;
}
.util ul li{
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 0 2px;
}
.util ul li + li::before{
  content: '|';
  color: #bbb;
  margin-right: 10px;
}
.util ul li a{
  color: inherit;
  transition: color 0.2s ease, opacity 0.2s ease;
}
.util ul li a:hover{
  color: #777;
}
.util ul li.sns a:hover img{
  opacity: 0.7;
}
.util ul li.sns a{
  display: inline-flex;
  align-items: center;
  gap: 6px;
}
.util ul li.sns img{
  width: 18px;
  height: 18px;
}


#nav{
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #eee;
}
#nav .nav-inner{
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
  position: relative;
}
#nav .nav-toggle{
  display: none;
  width: 44px;
  height: 44px;
  border-radius: 12px;
  border: 1px solid #ddd;
  background: #fff;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 4px;
  cursor: pointer;
}
#nav .nav-toggle span{
  width: 18px;
  height: 2px;
  background: #333;
  border-radius: 2px;
  transition: transform 0.2s ease, opacity 0.2s ease;
}
#nav.is-open .nav-toggle span:nth-child(1){
  transform: translateY(6px) rotate(45deg);
}
#nav.is-open .nav-toggle span:nth-child(2){
  opacity: 0;
}
#nav.is-open .nav-toggle span:nth-child(3){
  transform: translateY(-6px) rotate(-45deg);
}
#nav .nav-menu{
  display: flex;
  align-items: center;
  gap: 22px;
  font-size: 15px;
  color: #000;
  white-space: nowrap;
  flex: 1 1 auto;
  min-width: 0;
}
#nav .nav-menu li a{
  display: block;
  padding: 6px 0;
  color: #000;
  position: relative;
  transition: color 0.2s ease;
}
#nav .nav-menu li a::after{
  content: '';
  position: absolute;
  left: 0;
  bottom: -6px;
  width: 0%;
  height: 2px;
  background: #b5b5b5;
  transition: width 0.2s ease;
}
#nav .nav-menu li a:hover{
  color: #777;
}
#nav .nav-menu li a:hover::after{
  width: 100%;
}
.main{
  width: 100%;
  position: relative;
  margin-top: 24px;
}
.main .mySwiper{
  height: 676px;
  overflow: hidden;
  position: relative;
}

.main .imgB{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
}
.main .imgB img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.s-next,
.s-prev{
  position: absolute;
  width: 68px;
  height: 68px;
  top: 43%;
  transform: translateY(-50%);
  text-indent: -9999px;
  padding: 0;
  background: transparent;
  border: none;
  border-radius: 0;
  box-shadow: none;
  z-index: 10;
  cursor: pointer;
}
.s-prev{
  left: 74px;
}
.s-next{
  right: 74px;
}
.s-prev::before,
.s-next::before{
  content: '';
  position: absolute;
  width: 14px;
  height: 14px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  border-left: 3px solid #444;
  border-bottom: 3px solid #444;
}
.s-next::before{
  transform: translate(-50%, -50%) rotate(-135deg);
}
.main .main-controls{
  position: absolute;
  left: 50%;
  bottom: 22px;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  z-index: 12;
}
.main .main-pause{
  width: 38px;
  min-width: 38px;
  height: 38px;
  padding: 0;
  border: 1px solid #bcbcbc;
  border-radius: 50%;
  background: #fff;
  position: relative;
  cursor: pointer;
}
.main .main-pause:hover{
  background: #f7f7f7;
}
.main .main-pause:focus-visible{
  outline: 2px solid #222;
  outline-offset: 2px;
}
.main .main-pause::before,
.main .main-pause::after{
  content: '';
  position: absolute;
  top: 11px;
  width: 4px;
  height: 14px;
  border-radius: 2px;
  background: #222;
}
.main .main-pause::before{
  left: 11px;
}
.main .main-pause::after{
  right: 11px;
}
.main .main-pause.is-paused::before{
  top: 9px;
  left: 14px;
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 12px solid #222;
  border-radius: 0;
  background: transparent;
}
.main .main-pause.is-paused::after{
  display: none;
}
.main .main-pagination.swiper-pagination{
  position: static;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
}
.main .main-pagination .swiper-pagination-bullet{
  width: 12px;
  height: 12px;
  margin: 0 !important;
  opacity: 1;
  background: #d6d6d6;
  border: 1px solid #b4b4b4;
}
.main .main-pagination .swiper-pagination-bullet-active{
  border-radius: 50%;
  background: #222;
  border-color: #222;
}

.section{
  width: 1440px;
  height: auto;
  margin: 0 auto;
}
#section1{
  height: 706px;
  margin-top: 120px;
}
#section1 .swiper-button-next,
#section1 .swiper-button-prev{
  top: 230px;
}

#section1 p{
  text-align: left;
  /* color: red; */
}
#section1 .swiper-button-next,
#section1 .swiper-button-prev{
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: rgba(255,255,255,0.9);
  border: 1px solid #ddd;
  box-shadow: 0 4px 14px rgba(0,0,0,0.08);
  color: transparent;
}
#section1 .swiper-button-next:after,
#section1 .swiper-button-prev:after{
  display: none;
}
#section1 .swiper-button-next svg,
#section1 .swiper-button-prev svg{
  display: none;
}
#section1 .swiper-button-next:before,
#section1 .swiper-button-prev:before{
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  top: 50%;
  left: 50%;
  display: block;
  border-left: 2px solid #444;
  border-bottom: 2px solid #444;
  transform: translate(-50%, -50%) rotate(45deg);
}
#section1 .swiper-button-next:before{
  transform: translate(-50%, -50%) rotate(-135deg);
}
#section1 h2{
  width: 1440px;
  height: 62px;
  font-size: 28px;
  padding-bottom: 20px;
  text-align: left;
}
#section1 .boxWrap{
  height: 670px;
}
#section1 .boxWrap .slide{
  width: 1440px;
  height: 600px;
  padding: 20px 0;
  overflow: hidden;
}
#section1 .boxWrap .slide .img{
  width: 464px;
  height: 464px;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
}
#section1 .boxWrap .slide .img img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}
#section1 .boxWrap .slide .txt{
  width: 464px;
  height: auto;
  min-height: 94px;
}
#section1 .boxWrap .slide .txt p:nth-child(1){
  font-size: 18px;
  font-weight: 700;
  line-height: 1.3;
  min-height: 2.6em;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
#section1 .boxWrap .slide .txt p:nth-child(2){
  height: auto;
  font-size: 16px;
  line-height: 1.4;
  color: #888;
}
#section1 .boxWrap .slide .txt .price-original{
  text-decoration: line-through;
  color: #999;
}
#section1 .boxWrap .slide .txt p:nth-child(3){
  height: auto;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 500;
}

#section2{
  min-height: 580px;
  margin-top: 120px;
  position: relative;
  border-radius: 28px;
  overflow: hidden;
  background-color: #fee993;
  background-image: var(--section2-bg);
  background-repeat: no-repeat;
  background-position: left center;
  background-size: cover;
  display: flex;
  align-items: center;
  padding: 64px 72px;
}
#section2 .sec2-inner{
  width: 520px;
  margin-left: auto;
  display: flex;
  flex-direction: column;
  gap: 28px;
  text-align: left;
}
#section2 .sec2-copy h2{
  font-size: 36px;
  font-weight: 800;
  color: #262626;
  margin-bottom: 10px;
}
#section2 .sec2-copy p{
  font-size: 18px;
  color: #3b3b3b;
  line-height: 1.4;
}
#section2 .sec2-copy p + p{
  margin-top: 6px;
}
#section2 .sec2-cards{
  display: flex;
  flex-direction: column;
  gap: 18px;
}
#section2 .sec2-card{
  display: flex;
  gap: 16px;
  align-items: center;
  padding: 16px 18px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.08);
}
#section2 .sec2-thumb{
  width: 110px;
  height: 110px;
  border-radius: 12px;
  background: #fff7c9;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
#section2 .sec2-thumb img{
  width: 90px;
  height: 90px;
  object-fit: contain;
}
#section2 .sec2-info{
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
#section2 .sec2-title{
  font-size: 16px;
  font-weight: 700;
  color: #333;
  line-height: 1.3;
}
#section2 .sec2-price{
  display: flex;
  flex-direction: column;
  gap: 4px;
}
#section2 .sec2-origin{
  font-size: 14px;
  color: #9a9a9a;
  text-decoration: line-through;
}
#section2 .sec2-now{
  display: flex;
  align-items: baseline;
  gap: 8px;
}
#section2 .sec2-discount{
  color: #e04545;
  font-size: 18px;
  font-weight: 700;
}
#section2 .sec2-now strong{
  font-size: 22px;
  color: #222;
}
#section2 .sec2-point{
  font-size: 13px;
  color: #777;
}

#section3{
  margin-top: 120px;
}
#section3 .section3-head{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 16px;
  margin-bottom: 26px;
}
#section3 .section3-title{
  text-align: left;
}
#section3 .section3-kicker{
  color: #e06666;
  font-weight: 700;
  margin-bottom: 6px;
}
#section3 h2{
  font-size: 32px;
  color: #222;
  text-align: left;
}
#section3 .section3-more{
  font-size: 14px;
  color: #666;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  white-space: nowrap;
}
#section3 .boxWrap{
  height: auto;
}
#section3 .boxWrap>ul{
  width: 1440px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
}
#section3 .review-card{
  border-radius: 18px;
  background: #fff;
  overflow: hidden;
  border: 1px solid #eee;
  box-shadow: 0 12px 24px rgba(0,0,0,0.06);
}
#section3 .review-link{
  display: flex;
  flex-direction: column;
  height: 100%;
}
#section3 .review-img{
  width: 100%;
  height: 260px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f7f7f7;
}
#section3 .review-img img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}
#section3 .review-body{
  padding: 16px 18px 20px;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
#section3 .review-product{
  display: flex;
  align-items: center;
  gap: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #eee;
}
#section3 .review-product img{
  width: 46px;
  height: 46px;
  border-radius: 12px;
  object-fit: contain;
}
#section3 .review-product p{
  font-size: 15px;
  font-weight: 700;
  color: #333;
}
#section3 .review-meta{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#section3 .review-meta img{
  height: 16px;
}
#section3 .review-meta span{
  font-size: 13px;
  color: #999;
}
#section3 .review-text{
  font-size: 14px;
  color: #666;
  line-height: 1.55;
}

#section4{
  margin-top: 120px;
}
#section4>ul{
  width: 1440px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
}
#section4 ul li{
  border-radius: 24px;
  overflow: hidden;
  background: #f5f5f5;
  box-shadow: 0 10px 22px rgba(0,0,0,0.08);
}
#section4 ul li a{
  display: block;
}
#section4 ul li .img img{
  display: block;
  width: 100%;
  height: auto;
}

#footer{
  margin-top: 70px;
  background: #3b3b3b;
  color: #e1e1e1;
  padding: 32px 0 40px;
  font-size: 13px;
}
#footer .footer-inner{
  width: 1440px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1.1fr 1.4fr 1.6fr 0.9fr;
  gap: 24px;
}
#footer .footer-phone{
  display: block;
  font-size: 24px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 8px;
}
#footer .footer-hours{
  display: flex;
  flex-direction: column;
  gap: 6px;
  color: #cfcfcf;
}
#footer .footer-buttons{
  display: flex;
  gap: 12px;
  margin-top: 14px;
  width: 100%;
  justify-content: center;
}
#footer .footer-btn{
  background: #e5e5e5;
  color: #222;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
}
#footer .footer-contact p,
#footer .footer-company p{
  line-height: 1.45;
  color: #cfcfcf;
  margin-bottom: 8px;
}
#footer .footer-company-name{
  font-weight: 700;
  color: #fff;
  margin-bottom: 8px;
}
#footer .footer-brand{
  text-align: right;
  font-weight: 700;
  color: #fff;
  margin-bottom: 14px;
}
#footer .footer-family-wrap{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
}
#footer .footer-family{
  margin-left: auto;
  width: 180px;
  padding: 8px 12px;
  background: #4b4b4b;
  color: #fff;
  border: 1px solid #5f5f5f;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  cursor: pointer;
}
#footer .footer-family-panel{
  position: absolute;
  right: 0;
  bottom: 50px;
  width: 220px;
  background: #555;
  border-radius: 12px;
  padding: 6px 0;
  box-shadow: 0 14px 30px rgba(0,0,0,0.35);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(8px);
  transition: 0.2s ease;
}
#footer .footer-family-panel li + li{
  border-top: 1px solid rgba(255,255,255,0.12);
}
#footer .footer-family-panel a{
  display: block;
  padding: 14px 18px;
  color: #f1f1f1;
  font-weight: 600;
  transition: background-color 0.2s ease, color 0.2s ease;
}
#footer .footer-family-panel a:hover,
#footer .footer-family-panel a:focus-visible{
  background: #6a6a6a;
  color: #fff;
}
#footer .footer-family-wrap.is-open .footer-family-panel{
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateY(0);
}
#footer .footer-caret{
  font-size: 12px;
  transition: transform 0.2s ease;
}
#footer .footer-family-wrap.is-open .footer-caret{
  transform: rotate(180deg);
}
#footer .footer-links{
  display: flex;
  flex-direction: column;
  gap: 12px;
  text-align: right;
}
#footer .footer-links a{
  color: #e1e1e1;
}

#footer .footer-call{
  display: flex;
  flex-direction: column;
}
